import { Field, InputType } from "@nestjs/graphql";
import { IsBoolean, IsEmail, IsNotEmpty, IsString, MinLength } from "class-validator";
import { User } from "../../../../../orm-config/entity/authentication/user.entity";
import { ColumnExists } from "../../../../../validation/custom/column-exists/column-exists.decorator";


@InputType()
export class CreateUserAdminInput {

  @Field(() => String)
  @IsEmail()
  @IsNotEmpty()
  @ColumnExists(User)
  email: string;

  @Field(() => String)
  @MinLength(5)
  @IsNotEmpty()
  password: string;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  firstName: string;
  
  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  lastName: string;
  
  @Field(() => Boolean, { defaultValue: false })
  @IsBoolean()
  isActive: boolean;
  
  @Field(() => String)
  @IsString()
  profilePicture: string;
  
}
